<template>
  <div>
    <span>{{ avatar_label }}</span>
    <br />
    <div class="row my-2">
      <div class="col"></div>
      <div class="col-auto">
        <img :src="user.avatar" class="avatar-profile rounded-circle" />
        <button class="btn float-right">
          <icon icon="edit" size="sm" />
        </button>
      </div>
      <div class="col"></div>
    </div>
    <div v-for="(info, index) in infos" v-bind:key="index" class="my-1">
      <div class="row">
        <div class="col mt-2">
          <span >{{ info.label }}</span>
        </div>
        <div class="col-8 mr-2">
          <div class="input-group input-group-info">
            <input
              type="text"
              class="form-control input-info"
              disabled
              v-bind:value="info.value"
              v-bind:id="'edit' + index"
            />
            <button
              class="btn input-group-append btn-edit"
              v-on:click="enable_input(index)"
            >
              <icon icon="edit" size="sm" />
            </button>
          </div>
        </div>
      </div>
    </div>
    <a href="">Đăng xuất</a>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faEdit } from "@fortawesome/free-solid-svg-icons";
library.add({ faEdit });
export default {
  data() {
    return {
      avatar_label: "Ảnh đại điện",
      avatar_source:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcT_xTUvE4S36PXszCrf4ujpK47tF_zy19-QO_3jzZhSy2SAhlJP",
      infos: [
        {
          label: "Tên hiển thị:",
          value: this.user.name
        },
        {
          label: "Ngày sinh:   ",
          value: this.user.birthday
        },
        {
          label: "Email:       ",
          value: this.user.gmail
        },
        {
          label: "Liên hệ:     ",
          value: this.user.contact
        }
      ]
    };
  },
  methods: {
    enable_input: function(index) {
      console.log(index);
      var input = document.getElementById("edit" + index);
      input.disabled = false;
      input.select();
    }
  },
  props: {user:Object}
};
</script>

<style>
.avatar-profile {
  width: 200px;
  height: 200px;
  margin: auto;
}
.input-info {
  background-color: white !important;
  border-radius: 32px !important;
  border: none;
}
.input-group-info {
  background-color: white;
  /* margin:20px; */
  margin-top: 5px;
  margin-right: 10px !important;

  /* padding: 5px; */
  border-radius: 32px;
}
.btn-edit {
  border-radius: 32px !important;
}
</style>



