<template>
  <div class="my-block mr-2">
      <!-- <span>Chứa đựng:</span> -->
      <ul class="list-group-sm no-padding-inline">
          <li class="list-group-item" v-for="(content,index) in contents" v-bind:key="index">
              {{content.name}}
              <input v-bind:checked="content.checked" type="checkbox" class="float-right">
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    data(){
        return{
            contents:[
                {
                    name:"Hình ảnh",
                    checked:false
                },{
                    name:"Video",
                    checked:false
                },{
                    name:"Văn bản",
                    checked:false
                },{
                    name:"Đoạn ghi âm",
                    checked:false
                },
            ]
        }
    }

}
</script>

<style>
.list-group-item{
    border:none;
    background-color: transparent
}
</style>


