<template>
  <div class="accordion" id="timeline">
    <h3>Dòng thời gian</h3>
    <hr />
    <div v-for="(year, index) in years" v-bind:key="index">
      <CollapseButton :name="year.code+''" :icon="year.code+''" :show_icon="false"></CollapseButton>
      <div
        :class="'collapse '+((index == 0)?'show':'')"
        :id="'id_' + year.code + '_element'"
      >
        <Datepicker
          :disabled-dates="disabled_calendar_by_year(years.code)"
          :calendar-button="false"
          :typeable="true"
          :inline="true"
          :full-month-name="true"
          :bootstrap-styling="true"
          :language="calendar_lang"
          :draggable="true"
          maximum-view="month"
        ></Datepicker>
      </div>
    </div>
  </div>
</template>

<script>
import Datepicker from "vuejs-datepicker";
import CollapseButton from "./../shared/CollapseButton";
import { en, vi } from "vuejs-datepicker/dist/locale";
export default {
  name: "calendar",
  data() {
    return {
      years: [
        {
          code: 2019,
          numlog: 1000
        },
        {
          code: 2018,
          numlog: 1004
        }
      ],
      expanded: false,
      unit: "bài viết",
      calendar_lang: vi,
      date_picker: {
        "border-radius": "32px"
      }
    };
  },
  methods: {
    change_icon(index) {},
    disabled_calendar_by_year(year) {
      var state = {
        disabledDates: {
          to: new Date(year, 0, 1), // Disable all dates up to specific date
          from: new Date(year, 11, 30) // Disable all dates after specific date
        }
      };
      return state.disabledDates;
    }
  },
  components: { Datepicker, CollapseButton }
};
var state = {
  disabledDates: {
    to: new Date(2016, 0, 5), // Disable all dates up to specific date
    from: new Date(2016, 0, 26), // Disable all dates after specific date
    days: [6, 0], // Disable Saturday's and Sunday's
    daysOfMonth: [29, 30, 31], // Disable 29th, 30th and 31st of each month
    dates: [
      // Disable an array of dates
      new Date(2016, 9, 16),
      new Date(2016, 9, 17),
      new Date(2016, 9, 18)
    ],
    ranges: [
      {
        // Disable dates in given ranges (exclusive).
        from: new Date(2016, 11, 25),
        to: new Date(2016, 11, 30)
      },
      {
        from: new Date(2017, 1, 12),
        to: new Date(2017, 2, 25)
      }
    ],
    // a custom function that returns true if the date is disabled
    // this can be used for wiring you own logic to disable a date if none
    // of the above conditions serve your purpose
    // this function should accept a date and return true if is disabled
    customPredictor: function(date) {
      // disables the date if it is a multiple of 5
      if (date.getDate() % 5 == 0) {
        return true;
      }
    }
  }
};
</script>

<style>
.badge {
  color: white;
  background-color: red;
}
.accordion {
  border-radius: 32px;
  background-color: whitesmoke;
  padding: 20px;
  /* margin-right: 20px; */
  /* width: 50%; */
  height: 100%;
}
.date-picker {
  border-radius: 32px !important;
}
</style>



