import Repository from "./Repository"
import {setCookie, deleteAllCookies,getCookie} from "./cookieRepository"
import 'babel-polyfill'
const resource = "/user";
export default {
    login: async function (googleUser) {
        try {
            let result = await Repository.post(`${resource}/login`, googleUser);
            if (result.status == 201) {
                console.log(result.data);
                setCookie("jhash",result.data.token);
                setCookie("_id",result.data._id);
                return Promise.resolve(result.data);
            } else return Promise.reject()
        } catch (e) { return Promise.reject() }
    },
    getProfile: async function(userID) {
        try {
            let result = await Repository.get(`${resource}/profile`,{params:{userID:userID}});
            if (result.status == 200) {
                console.log(result.data);
                return Promise.resolve(result.data);
            } else return Promise.reject()
        } catch (e) { return Promise.reject() }
    }
}


