<template>
  <div class="my-block mr-2">
    <!-- <span>Cảm xúc</span> -->
    <div class="row">
      <div class="col col-icon" v-for="(feel,i) in  feels" v-bind:key="i">
        <div class="feel-info">
        <span class="num-feels">{{feel.num_post}}</span>
        <input type="checkbox" name="" v-bind:id="'feelcheck'+i">
        </div>
        <img class="icon-feeling" v-bind:src="feel.icon" />
      </div>

    </div>
  </div>
</template>

<script>
import GoodActive from "../../shared/assets/good_active.png";
import HateActive from "../../shared/assets/hate_active.png";
import NaturalActive from "../../shared/assets/natural_active.png";
import ExcellentActive from "../../shared/assets/excellent_active.png";
import DisappointedActive from "../../shared/assets/disappointed_active.png";
export default {
  data() {
    return {
        feels:[{
            icon:ExcellentActive,
            num_post:233
        },{
            icon:GoodActive,
            num_post:636
        },{
            icon:NaturalActive,
            num_post:453
        },{
            icon:DisappointedActive,
            num_post:23
        },{
            icon:HateActive,
            num_post:231
        },]
    };
  }
};
</script>

<style>
.icon-feeling {
  width: 50px;
  /* margin-left: 5px; */
  cursor: pointer;
}
.num-feels {
  font-size: 10px;
  color: red;
}
.col-icon{
    padding: 0 !important;
    padding-left: 10px !important;
    margin:0 !important;
}
.feel-info{
    margin-left: 30px
}
</style>



