<template>
  <div>
    <CollapseButton icon="quote-left" name="Nội dung"></CollapseButton>
    <ContentFilter class="collapse show" id="id_quote-left_element"></ContentFilter>
    <CollapseButton icon="grin-beam" name="Cảm xúc"></CollapseButton>
    <FeelingFilter class="collapse show" id="id_grin-beam_element"></FeelingFilter>
    <CollapseButton icon="air-freshener" name="Chủ đề"></CollapseButton>
    <TopicFilter class="collapse show" id="id_air-freshener_element"></TopicFilter>
  </div>
</template>

<script>
import CollapseButton from "./../../shared/CollapseButton";
import ContentFilter from "./ContentFilter";
import FeelingFilter from "./FeelingFilter";
import TopicFilter from "./TopicFilter";
export default {
  data() {
    return {};
  },
  components: {
    CollapseButton,
    TopicFilter,
    ContentFilter,
    FeelingFilter
  }
};
</script>

<style>

</style>



