<template>
  <div class="extensiontab">
    <div class="row">
      <div class="col-6">
        <h3>{{ tabs[active_tab].tab_name }}</h3>
      </div>
      <div class="col-6 mt-1">
        <button v-if="tabs[active_tab].btn_fn_name != ''" class="btn btn-function float-right">{{ tabs[active_tab].btn_fn_name }}</button>
      </div>
    </div>
    <hr class="mr-3" />
    <Filters class="scroll_extensions" v-if="active_tab == 0" :user="user"></Filters>
    <Profile class="scroll_extensions" v-if="active_tab == 1" :user="user"></Profile>
    <Setting class="scroll_extensions" v-if="active_tab == 2" :user="user"></Setting>
  </div>
</template>

<script>
import Profile from "./Profile";
import Filters from "./filters/Filters";
import Setting from "./settings/Setting";
export default {
  components: {
    Profile,
    Setting,
    Filters
  },
  data() {
    return {
      tabs: [
        {
          tab_name: "Bộ lọc",
          btn_fn_name: "Áp dụng"
        },
        {
          tab_name: "Hồ sơ cá nhân",
          btn_fn_name: "Cập nhập"
        },
        {
          tab_name: "Cài đặt",
          btn_fn_name: ""
        }
      ]
    };
  },
  props: {
    active_tab: 0,
    user:Object
  }
};
</script>

<style>
.extensiontab {
  border-radius: 32px;
  background-color: whitesmoke;
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  height: 98vh;
  /* margin-right: 200px; */
  /* padding-right: 50px; */
}
.scroll_extensions {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 86vh;
}
.scroll_extensions::-webkit-scrollbar {
  width: 3px;
}

.scroll_extensions::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 32px;
}

.scroll_extensions::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 32px;
}

.scroll_extensions::-webkit-scrollbar-thumb:hover {
  background: red;
}
.btn-function{
  border-radius: 32px;
  margin-right: 10px;
}
.btn-function:hover{
  background-color:turquoise;
  border-radius: 32px;
  margin-right: 10px;
}
.btn-function:active{
  background-color:turquoise !important;
  border-radius: 32px;
  margin-right: 10px;
}
</style>



