<template>
  <div class="editor mr-2">
    <loading :active.sync="isLoading" :is-full-page="false"></loading>
    <div class="input-group">
      <img class="avatar on-top mr-1 mt-1" v-bind:src="user.avatar" />
      <textarea-autosize
        class="input-new-post form-control"
        :placeholder="placeholder"
        v-model="textContents.title"
        @input="changeStatus"
      />
    </div>
    <hr />
    <div class="row">
      <vue-feedback-reaction
        @input="changeFeeling"
        class="audios-div col-auto"
        :value="textContents.feeling"
        v-show="is_browsing_type[3].status"
      />
      <div class="audios-div col justify-content-center" v-show="is_browsing_type[2].status">
        <template v-if="showError">Trình duyệt không hỗ trợ ghi âm hoặc bạn đã khóa quyền ghi âm :((</template>
        <template v-else>
          <dictaphone @stop="handleRecording($event)" @error="handleError" mime-type="audio/mp3">
            <template slot-scope="{ isRecording, startRecording, stopRecording}">
              <div class="row container">
                <div class="col-auto align-self-center">
                  <VueRecordAudio @stream="startRecording" @result="stopRecording" />
                </div>
                <div class="col align-self-center">
                  <spectrum-analyser :style="{ opacity: isRecording ? 1 : .4 }" />
                </div>
              </div>
            </template>
          </dictaphone>
          <div v-if="audioSources.length">
            <audio
              :src="audioSources[currentAudioIndex]"
              controls
              style="display:inline-block; outline:none !important; border:none; width:90%"
            ></audio>
            <button class="close" style="outline:none !important" v-on:click="deleteAudio">
              <icon
                icon="trash-alt"
                size="lg"
                class="npost-icons float-left mt-2 mr-2"
              />
            </button>
          </div>
          <div class="mb-2">
            <button
              v-for="(audio,i) in audioSources"
              v-bind:key="'audio'+i"
              :class="'mx-2 my-2 btn mborder '+ ((i==currentAudioIndex)?'btn-primary':'')"
              style="width:100px;height:50px"
              v-on:click="currentAudioIndex=i"
            >
              <icon icon="file-audio" size="lg" class="npost-icons" />
              <span>{{i+1}}</span>
            </button>
          </div>
        </template>
      </div>
    </div>
    <div>
      <VueUploadMultipleImage
        class="mb-2"
        v-show="is_browsing_type[0].status"
        @images_changed="updateImages"
        @limit-exceeded="limitErrorShow"
        :showPrimary="false"
        :images="imageSources"
      ></VueUploadMultipleImage>
      <VideoUpload
        class="mb-2"
        v-show="is_browsing_type[1].status"
        :showPrimary="false"
        @videos_changed="updateVideos"
        @limit-exceeded="limitErrorShow"
        :videos="videoSources"
      ></VideoUpload>
      <button class="m-btn mr-2" v-on:click="browse('image')">
        <icon icon="image" size="lg" class="npost-icons float-left mx-2" />
        <span class="mr-2">Ảnh</span>
        <span
          class="float-right number-media-contents"
          v-show="this.mediaContents.images.length>0"
        >+{{this.mediaContents.images.length}}</span>
      </button>
      <button class="m-btn mr-2" v-on:click="browse('video')">
        <icon icon="video" size="lg" class="npost-icons float-left mx-2" />
        <span class="mr-2">Video</span>
        <span
          class="float-right number-media-contents"
          v-show="this.mediaContents.videos.length>0"
        >+{{this.mediaContents.videos.length}}</span>
      </button>
      <button class="m-btn mr-2" v-on:click="browse('audio')">
        <icon icon="microphone" size="lg" class="npost-icons float-left mx-2" />
        <span class="mr-2">Ghi âm</span>
        <span
          class="float-right number-media-contents"
          v-show="this.mediaContents.audios.length>0"
        >+{{this.mediaContents.audios.length}}</span>
      </button>
      <button class="m-btn" v-on:click="browse('feeling')">
        <icon
          v-show="(textContents.feeling=='')"
          icon="laugh"
          size="lg"
          class="npost-icons float-left mx-2"
        />
        <span class="mr-2">Cảm xúc</span>
        <img
          v-show="textContents.feeling!=''"
          width="30px"
          class="npost-icons"
          :src="feelingArr[textContents.feeling]"
          alt
        />
      </button>
      <!-- <button class="m-btn">
        <icon icon="ellipsis-h" class="icons float-left ml-2" />
        <span>More</span>
      </button>-->
    </div>
    <button
      class="btn btn-primary mborder form-control mt-2"
      v-bind:disabled="!haveContent"
      v-show="haveContent"
      v-on:click="postToServer"
    >Đăng</button>
  </div>
</template>

<script>
import VueUploadMultipleImage from "./new_post_components/VueUploadMultipleImage";
import VideoUpload from "./new_post_components/VideoUpload";
import VueFeedbackReaction from "../shared/VueFeedbackReaction";
import postRepository from "../../repositories/postRepository";
import Loading from "vue-loading-overlay";

import "vue-loading-overlay/dist/vue-loading.css";
import { library } from "@fortawesome/fontawesome-svg-core";
import GoodActive from "../shared/assets/good_active.png";
import HateActive from "../shared/assets/hate_active.png";
import NaturalActive from "../shared/assets/natural_active.png";
import ExcellentActive from "../shared/assets/excellent_active.png";
import DisappointedActive from "../shared/assets/disappointed_active.png";
import Dictaphone from "./new_post_components/Dictaphone";
import SpectrumAnalyser from "./new_post_components/SpectrumAnalyser";
import {
  faTrashAlt,
  faImage,
  faVideo,
  faMicrophone,
  faLaugh,
  faEllipsisH,
  faFileAudio
} from "@fortawesome/free-solid-svg-icons";

library.add(
  faImage,
  faVideo,
  faMicrophone,
  faLaugh,
  faEllipsisH,
  faTrashAlt,
  faFileAudio
);
let vm = this;
export default {
  components: {
    VueUploadMultipleImage,
    VideoUpload,
    VueFeedbackReaction,
    Loading,
    Dictaphone,
    SpectrumAnalyser
  },
  props: { user: Object },
  data() {
    return {
      showError: false,
      isLoading: false,
      haveContent: false,
      currentAudioIndex: 0,
      placeholder: "Hôm nay có gì hay?",
      feelingArr: [
        ExcellentActive,
        GoodActive,
        NaturalActive,
        DisappointedActive,
        HateActive
      ],
      is_browsing_type: [
        {
          name: "image",
          status: false
        },
        {
          name: "video",
          status: false
        },
        {
          name: "audio",
          status: false
        },
        {
          name: "feeling",
          status: false
        }
      ],
      textContents: {
        title: "",
        feeling: ""
      },
      mediaContents: {
        images: [],
        videos: [],
        audios: []
      },
      audioSources: [],
      imageSources: [],
      videoSources: []
    };
  },
  methods: {
    limitErrorShow(e){
      console.log(e.amount,"/",e.max,"(",e.type,")");
    },
    handleError() {
      this.showError = true;
    },
    deleteAudio() {
      this.mediaContents.audios.splice(this.currentAudioIndex, 1);
      this.audioSources.splice(this.currentAudioIndex, 1);
      this.changeStatus();
    },
    // eslint-disable-next-line no-unused-vars
    handleRecording({ blob, src }) {
      console.log("new audio");
      this.audioSources.push(src);
      this.mediaContents.audios.push(blob);
      this.changeStatus();
    },
    browse(str) {
      console.log("type ", str);
      this.is_browsing_type.forEach((type, i) => {
        if (type.name == str) this.is_browsing_type[i].status = !type.status;
        else this.is_browsing_type[i].status = false;
      });
      console.log(this.is_browsing_type);
    },
    postToServer() {
      // console.log(this.userPost);
      this.isLoading = true;
      postRepository
        .uploadPost(this.textContents, this.mediaContents)
        .then(r => {
          console.log("đăng bài thành công");
          this.textContents.title = "";
          this.textContents.feeling = "";
          this.mediaContents.images = [];
          this.mediaContents.videos = [];
          this.mediaContents.audios = [];
          this.audioSources = [];
          this.videoSources=[];
          this.imageSources=[];
          this.is_browsing_type.forEach(type=>{
            type.status=false;
          })
          this.changeStatus();
          this.$emit("new_post_created");
          this.isLoading = false;
        })
        .catch(e => console.log("đăng bài thất bại"));
    },
    updateImages(imageList) {
      console.log("data", imageList);
      this.mediaContents.images = imageList;
      this.imageSources = [];
      this.mediaContents.images.forEach(element => {
        this.imageSources.push({
          name: element.name,
          path: URL.createObjectURL(element),
          highlight: 1,
          default: 1
        });
      });
      this.changeStatus();
    },
    updateVideos(videoList) {
      console.log(videoList);
      this.mediaContents.videos = videoList;
      this.videoSources = [];
      this.mediaContents.videos.forEach(element => {
        this.videoSources.push({
          name: element.name,
          path: URL.createObjectURL(element),
          highlight: 1,
          default: 1
        });
      });
      this.changeStatus();
    },
    changeStatus() {
      if (
        this.textContents.title.trim() != "" ||
        this.mediaContents.images.length > 0 ||
        this.mediaContents.videos.length > 0 ||
        this.mediaContents.audios.length > 0 ||
        this.textContents.feeling != ""
      ) {
        this.haveContent = true;
      } else this.haveContent = false;
    },
    changeFeeling(e) {
      if (e == -1) {
        this.textContents.feeling = "";
        this.browse("feeling");
        return;
      }
      this.textContents.feeling = e;
    }
  }
};
</script>

<style>
.editor {
  background-color: rgb(255, 255, 255);
  border-radius: 32px;
  border: solid 1px rgb(177, 177, 177);
  padding: 20px;
  position: relative;
  /* margin-inline: 10px; */
}
.m-btn {
  /* font-size: 30px; */
  border-radius: 32px;
  background-color: whitesmoke !important;
  border: none;
  outline: none !important;
  padding: 10px;
  /* margin: 20px; */
}
.m-btn:hover {
  /* font-size: 30px; */
  border-radius: 32px;
  background-color: turquoise !important;
  border: none;
  outline: none !important;
  padding: 10px;
  cursor: pointer;
  /* margin: 20px; */
}
.npost-icons {
  color: black;
  /* width: 40px; */
  /* height: 40px; */
  margin: auto;
  /* border: solid 1px; */
  /* border-radius: 32px; */
}
.input-new-post {
  border-radius: 32px !important;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.btn-post-submit {
  background-color: #91c771;
  color: white;
}
.btn-post-submit:disabled {
  color: black;
}
.number-media-contents {
  color: red;
  font-style: oblique;
  font-size: 20px;
}
.audios-div {
  border: 1px dashed #d6d6d6;
  border-radius: 32px;
  background-color: #fff;
  margin: 10px;
}
.on-top {
  margin-top: 0;
}
</style>



