<template>
  <div class="scroll_body">
    <loading
      :active.sync="isLoading"
      :is-full-page="false"
    ></loading>
    <button hidden id="showDeleteModalBtn" data-toggle="modal" data-target="#deleteModal"></button>
    <button hidden id="showShareModalBtn" data-toggle="modal" data-target="#shareModal"></button>
    <button hidden id="showFreezeModalBtn" data-toggle="modal" data-target="#freezeModal"></button>
    <DeletePostModal
      v-on:delete="delete_post"
      v-on:freeze="callDeleteAction();callFreezeAction()"
      v-on:edit="callDeleteAction(),callEditAction()"
    ></DeletePostModal>
    <SharePostModal v-on:share="share_post"></SharePostModal>
    <FreezePostModal v-on:freeze="freeze_post"></FreezePostModal>
    <NewPostEditor class="shadow" :user="user" @new_post_created="refreshPosts"></NewPostEditor>
    <div class="my-3" v-for="(post, index) in posts" v-bind:key="index">
      <Post
        class="shadow"
        :index="index"
        :post="post"
        v-on:delete_action="callDeleteAction"
        v-on:freeze_action="callFreezeAction"
        v-on:share_action="callShareAction"
        v-on:edit_action="callEditAction"
        :user="user"
      />
    </div>
  </div>
</template>

<script>
import NewPostEditor from "./NewPostEditor";
import Post from "./Post";
import postRepository from "../../repositories/postRepository";
import DeletePostModal from "./post_components/DeletePostModal";
import SharePostModal from "./post_components/SharePostModal";
import FreezePostModal from "./post_components/FreezePostModal";
import Loading from "vue-loading-overlay";

export default {
  props: { user: Object },
  data() {
    return {
      isLoading:false,
      active_index: 0,
      posts: []
    };
  },
  components: {
    NewPostEditor,
    Post,
    DeletePostModal,
    FreezePostModal,
    SharePostModal,
    Loading
  },
  async beforeCreate() {
    this.isLoading=true;
    console.log("what, i am in mounted body");
    this.posts = await postRepository.getListPost(0);
    this.isLoading=false;
  },
  methods: {
    callDeleteAction(e) {
      //hàm chỉ có tác dụng gọi modal xác thực
      console.log(e);
      document.getElementById("showDeleteModalBtn").click();
      if (e != undefined && e.index != undefined) this.active_index = e.index;
    },
    callEditAction(e) {
      console.log(e);
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      console.log("edit in ", this.active_index);
      //hàm chỉ có tác dụng gọi modal xác thực
      // document.getElementById("showDeleteModalBtn").click();
      // this.active_index = e.index;
    },
    callShareAction(e) {
      console.log(e);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showShareModalBtn").click();
    },
    callFreezeAction(e) {
      console.log(e);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showFreezeModalBtn").click();
    },
    async edit_post(e) {},
    async share_post(e) {
      //đóng modal
      document.getElementById("showShareModalBtn").click();
      console.log(e);
    },
    async freeze_post() {
      //đóng modal
      document.getElementById("showFreezeModalBtn").click();
      console.log("triggle freeze");
      try {
        this.isLoading=true;
        await postRepository.freezePost(this.posts[this.active_index]._id);
        this.posts.splice(this.active_index, 1);
        this.isLoading=false;
      } catch (error) {
        console.log("lỗi đóng băng kỉ niệm",error);
      }
    },
    async delete_post() {
      //đóng modal
      document.getElementById("showDeleteModalBtn").click();
      console.log("triggle delete");
      try {
        this.isLoading=true;
        await postRepository.deletePost(this.posts[this.active_index]._id);
        this.posts.splice(this.active_index, 1);
        this.isLoading=false;
      } catch (error) {
          console.log("lỗi xóa kỉ niệm",error);
      }
    },
    async refreshPosts(){
      this.posts = await postRepository.getListPost(0);
    }
  }
};
</script>

<style>
.scroll_body {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 95vh;
}
.scroll_body::-webkit-scrollbar {
  width: 0px;
}
/* 
.scroll_body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 32px;
}
 
.scroll_body::-webkit-scrollbar-thumb {
  background: gray; 
  border-radius: 32px;
}

.scroll_body::-webkit-scrollbar-thumb:hover {
  background:red; 
} */
</style>



