<template>
  <ul class="list-group-sm mr-2 my-block">
    <li class="list-group-item" v-for="(device, i) in devices" v-bind:key="i">
      <div class="row">
        <icon
          class="col-auto mt-2"
          v-bind:icon="device.type == 'mobile' ? 'tablet-alt' : 'desktop'"
          pull="right"
          size="lg"
        />
        <div class="col">
          <button class="btn btn-sm btn-primary float-right">
            {{ logout_str }}
            <icon
              class="mt-1"
              icon="sign-out-alt"
              pull="right"
              size="md"
            />
          </button>
          <span>{{ device.name }}</span>
          <p class="mini-text">
            ({{ last_active_str }}{{ device.last_active }})
          </p>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faTabletAlt,
  faDesktop,
  faSignOutAlt
} from "@fortawesome/free-solid-svg-icons";

library.add(faTabletAlt, faDesktop, faSignOutAlt);
export default {
  data() {
    return {
      logout_str: "Đăng xuất",
      last_active_str: "Hoạt động lần cuối: ",
      devices: [
        {
          name: "Q20",
          type: "mobile",
          last_active: "12/12/2018"
        },
        {
          name: "Linh's Imac",
          type: "desktop",
          last_active: "12/10/2019"
        }
      ]
    };
  }
};
</script>

<style>
.list-group-item{
    border:none;
    margin-bottom: 0;
}
</style>



