import Repository from "./Repository"
import 'babel-polyfill'
import {forEach} from "lodash"
import {baseDomain} from "./Repository"
const resource = "/posts";
export default {
    async getListPost(start) {
        try {
            let result = await Repository.get(`${resource}`, { params: { start: start,includeFreezedPost:false } });
            console.log(result.status);
            if (result.status == 200) {
                _.forEach(result.data,(post,pi)=>{
                    _.forEach(post.images,(img,ii)=>{
                        result.data[pi].images[ii]= baseDomain + "/img/"+ img;
                    });
                    _.forEach(post.videos,(vid,ii)=>{
                        result.data[pi].videos[ii]= baseDomain + "/vid/"+ vid;
                    });
                    _.forEach(post.audios,(aud,ii)=>{
                        result.data[pi].audios[ii]= baseDomain + "/aud/"+ aud;
                    })
                })
                return Promise.resolve(result.data);
            } else return Promise.reject()
        } catch (e) { return Promise.reject() }
    },
    async getPost(postID) {
        return Repository.get(`${resource}/${postID}`,{params:{includeFreezedPost:true}});
    },
    async uploadPost(textContents, mediaContents) {
        let data = new FormData();
        data.append('textContent', JSON.stringify(textContents));
        for (let i = 0; i < mediaContents.images.length; i++) {
            data.append('images', mediaContents.images[i]);
        }
        for (let i = 0; i < mediaContents.videos.length; i++) {
            data.append('videos', mediaContents.videos[i]);
        }
        for (let i = 0; i < mediaContents.audios.length; i++) {
            data.append('audios', mediaContents.audios[i]);
        }
        console.log("form ", data)
        try {
            let result = await Repository.post(`${resource}/create`, data);
            console.log(result.status);
            if (result.status == 201) {
                return Promise.resolve(result.data);
            } else return Promise.reject()
        } catch (e) { return Promise.reject() }

    },
    async deletePost(postID) {
        try {
            let result = await Repository.delete(`${resource}`, { params: { postID: postID } });
            console.log(result.status);
            if (result.status == 204) {
                return Promise.resolve();
            } else return Promise.reject()
        } catch (e) { return Promise.reject() }
    },
    async updatePost(postID, updatedTextContents, updatedMediaContents = null) {
        let updatedData = new FormData();
        updatedData.append('textContent', JSON.stringify(updatedTextContents));
        if (updatedMediaContents != null) {
            for (let i = 0; i < updatedMediaContents.images.length; i++) {
                data.append('images', updatedMediaContents.images[i]);
            }
            for (let i = 0; i < updatedMediaContents.videos.length; i++) {
                data.append('videos', updatedMediaContents.videos[i]);
            }
            for (let i = 0; i < updatedMediaContents.audios.length; i++) {
                data.append('audios', updatedMediaContents.audios[i]);
            }
        }
        try {
            let result = await Repository.put(`${resource}`, updatedData, { params: { postID: postID } });
            console.log(result.status);
            if (result.status == 205) {
                return Promise.resolve();
            } else return Promise.reject()
        } catch (e) { return Promise.reject(e) }
    },
    async freezePost(postID) {
        try {
            let updatedPost = { status: "freezed" };
            await this.updatePost(postID, updatedPost);
            return Promise.resolve();
        } catch (e) { return Promise.reject(e) }
    },
}


