<template>
  <form class="input-group input-group-search">
    <input
      class="form-control transparent"
      type="search"
      v-bind:placeholder="placeholder"
      aria-label="Search"
    />
    <button class="btn input-group-append transparent">
      <icon
            v-on:click="change_active_tab('filter')"
            icon="search"
            class="float-right ml-2"
          />
    </button>
  </form>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
library.add({faSearch})
export default {
  data() {
    return {
      placeholder:"Tìm kiếm nội dung | chủ đề | cảm xúc ......."
    };
  }
};
</script>

<style>
.input-group-search{
  border-radius: 32px;
  background-color:white;
  padding: 2px;
}
.transparent{
  background-color:transparent;
  border:transparent;
  outline:none;
}

</style>



