<template>
  <!-- Button trigger modal -->

  <!-- Modal -->
  <div
    class="modal fade mborder"
    id="shareModal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="shareModalTitle"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="shareModalTitle">
            {{ share_modal_title }}
          </h5>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="shareByLinkDiv my-block mborder">
            <span class="mb-2">Liên kết chia sẻ</span>
            <SwitchElement class="mt-2 mx-2"></SwitchElement>
            <button class="btn btn-sm btn-link float-right">Sao chép</button>
            <input type="text" class="form-control mborder mt-2" />
          </div>
          <div class="shareForPeople my-block mborder group-input">
            <span class="mb-2">Chia sẻ với</span>
            <div class="row ">
              <div
                class="col-auto no-padding mt-2"
                v-for="(email, i) in emails"
                v-bind:key="i"
              >
                <span class="span-block mborder "
                  >{{ email }}
                  <icon
                    icon="user-minus"
                    class="icon-btn mt-1"
                    v-on:click="remove_email(i)"
                  ></icon>
                </span>
              </div>
            </div>

            <input
              type="email"
              class="form-control mborder mt-2"
              placeholder="Nhập email"
              v-on:keyup="add_email"
              v-model="current_input"
            />
            <button
              v-on:click="$emit('share',{emails:emails})"
              type="button"
              class="btn btn-sm float-right mt-3 btn-primary mborder"
            >
              {{ share_btn_str }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SwitchElement from "./../../shared/SwitchElement";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faTimesCircle, faUserMinus } from "@fortawesome/free-solid-svg-icons";
library.add(faTimesCircle, faUserMinus);
export default {
  data() {
    return {
      current_input: "",
      emails: [],
      share_modal_title: "Chia sẻ kỷ niệm?",
      share_btn_str: "Chia sẻ"
    };
  },
  components: {
    SwitchElement
  },
  methods: {
    remove_email(index) {
      this.emails.splice(index, 1);
    },
    add_email(e) {
      if (e.code == "Enter") {
        let email = this.current_input;
        if (validateEmail(email)) {
          this.emails.push(email);
          this.current_input = "";
        }
      }
    }
  }
};
function validateEmail(email) {
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}
</script>

<style>
.shareByLinkDiv {
  background-color: rgba(0, 0, 0, 0.055);
}
.shareForPeople {
  border: solid 1px whitesmoke;
}
.span-block {
  margin: 5px;
  margin-top: 20px;
  background-color: rgba(0, 0, 0, 0.089);
  padding: 5px;
  font-size: 15px;
}
.icon-btn {
  cursor: pointer;
  padding: 2px;
}
.icon-btn:hover {
  border-radius: 32px;
  background-color: white;
}
</style>



