<template>
  <div>
    <LoginScreen v-if="!isLoged" v-on:logged_in="change_user"></LoginScreen>
    <div v-if="isLoged">
      <NavigatorBar class="sticky-top" v-on:myEvent="change_extension_tab" :user="user"></NavigatorBar>
      <div class="container-fluid">
        <div class="row my-2">
          <div class="col-md-3">
            <Calendar></Calendar>
          </div>
          <div class="col-md-6">
            <Body :user="user"></Body>
          </div>
          <div class="col-md-3">
            <ExtensionTab v-bind:active_tab="active_tab" :user="user"></ExtensionTab>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import Calendar from "./components/calendar/calendar.vue";
import ExtensionTab from "./components/extensions/ExtensionTab.vue";
import NavigatorBar from "./components/navigationbar/NavigatorBar";
import LoginScreen from "./LoginScreen";
import Body from "./components/body/Body.vue";
import {getCookie} from "./repositories/cookieRepository"
import userRepository from "./repositories/userRepository"

export default {
  name: "app",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      active_tab: 0,
      isLoged:false,
      user:{}
    };
  },
  async beforeCreate(){
    let uid= getCookie("_id");
    let jhash= getCookie("jhash");
    console.log("id :",uid," hash :",jhash);
    if(uid==null || jhash==null) this.isLoged=false;
    else{
      try {
        let userProfile= await userRepository.getProfile(uid);
        console.log(userProfile)
        this.user=userProfile;
        this.isLoged=true
      } catch (error) {
        this.isLoged=false
        console.log(error)
        return;
      }
    }
  },
  
  components: {
    Calendar,
    ExtensionTab,
    NavigatorBar,
    Body,
    LoginScreen
  },
  methods: {
    change_extension_tab(e) {
      console.log(e);
      if (e.tab_id == "settings") {
        this.active_tab = 2;
      } else if (e.tab_id == "profile") {
        this.active_tab = 1;
      } else this.active_tab = 0; //filter
    },
    change_user(e){
      console.log(e);
      this.isLoged=!this.isLoged;
      this.user=e;
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
/* .scroll-inside{
  overflow-y: scroll;
} */
/* .position-fixed{
  border-radius: 32px;
  background-color: whitesmoke;
  padding: 20px;
} */
/* ::-webkit-scrollbar {
  width: 0px; 
} */
.my-block {
  border-radius: 32px;
  border: 1px;
  margin-bottom: 10px;
  margin-right: 10px;
  background-color: white;
  padding: 10px;
}
.no-padding-inline {
  padding-inline-start: 0px;
}
.mborder {
  border-radius: 32px !important;
}
.my-center {
  display: inline-block;
  position: relative;
  top: 40%;
  width: 90%;
  margin: 0 auto;
  z-index: 9999990;
}
</style>



