import axios from 'axios';
import {getCookie} from "./cookieRepository"
// export const baseDomain="http://localhost:9000"
export const baseDomain="https://qdiary.herokuapp.com"
const baseUrl=`${baseDomain}/api`;
console.log(baseUrl)
const jhash= getCookie("jhash");
export default axios.create({
    baseURL:baseUrl,
    headers: {"Authorization":jhash}
})


