<template>
  <div v-if="show" class="my-popup container">
    <div v-bind:class="{'my-block my-center ':true,'my-shake-animation' : wrong}">
      <div class="container-fluid">
        <h4 style="text-align:center">{{ header }}</h4>
        <p>{{ title }}</p>
        <input
          :id="caller_id + 'input_password'"
          type="password"
          v-bind:class="'form-control my-border-radius mt-2 '+ (wrong ? 'wrong-input' : '')"
        />
        <button
          v-on:click="checkPassword"
          class="btn btn-primary my-btn mt-2 float-right"
        >
          {{ ok }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      header: "Yêu cầu xác thực",
      ok: "Xác nhận",
      wrong: false
    };
  },
  props: {
    title: String,
    show: Boolean,
    caller_id: String
  },
  methods: {
    checkPassword() {
      // this.wrong = false;
      setTimeout(this.wrong=false,3000);
      console.log("checking pass");
      console.log(this.caller_id);
      let pass = document.getElementById(this.caller_id + "input_password")
        .value;
      if (pass == "0000") this.$emit("correct",{});
      else {
        this.wrong = true;
        this.$emit("incorrect",{});
        
      }
      console.log(pass);
    }
  }
};
</script>

<style>
.my-popup {
  background-color: rgba(0, 0, 0, 0.555);
  height: 100%;
  width: 92%;
  margin-right: 20px;
  border-radius: 32px;
  position: absolute;
  top: 1px;
  z-index: 999;
  left: 19px;
  text-align: center;
  /* display: inline-block; */
}

.my-btn {
  border-radius: 32px;
}
.my-border-radius {
  border-radius: 32px;
}
.wrong-input{
  border: solid 1px red !important;
}
.my-shake-animation {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}
@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
</style>



